<template>
    <div class="content" id="projects">
        <h3 class="text-center mb-5">Projects</h3>
        <div class="row">
            <div v-for="(item, index) in itemToShow" :key="index" class="card-columns d-flex col-lg-3 col-md-4 col-sm-6 col-md-6 col mb-3">
                <div class="card projects border-0 rounded-0 bg-transparent">
                    <div class="card-header border-0 p-0 mb-2">
                    <img @click="zoomImage($event)" v-bind:src="projects[index].thumbnail" v-bind:data-title="projects[index].title" v-bind:data-client="projects[index].client" @mouseover="hover = true" @mouseleave="hover = false"/> 
                    </div>
                    <div class="card-body p-0">
                        <span>Project: {{ projects[index].title }}</span>
                    </div>
                </div>
                
            </div>
        </div>
        <div v-if="itemToShow < projects.length">
            <button @click="itemToShow += 4" class="btn btn-block border-dark">Show More</button>
        </div>        
        <div v-else></div>

        <div ref="modal" class="modal fade" :class="{show, 'd-block': active}" tabindex="-1" role="dialog">
            <div class="modal-dialog modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <div class="d-block">
                            <h5 class="modal-title" id="modal-title"></h5>
                            <div id="modal-client"></div>
                        </div>
                        <button @click="zoomImage()" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body p-2">
                        <img id="modal-img" src="">
                    </div>
                </div>
            </div>
        </div>
        <div v-if="active" class="modal-backdrop fade show"></div>

    </div>
</template>
<script>

export default {
    methods: {
        zoomImage () {	
            let element = event.currentTarget
            let src = element.getAttribute('src');
            let title = element.getAttribute('data-title');
            let client = element.getAttribute('data-client');

            const body = document.querySelector("body");
            this.active = !this.active;
            this.active
                ? body.classList.add("modal-open")
                : body.classList.remove("modal-open");
            setTimeout(() => (this.show = !this.show), 10);
            
            let target_img = document.getElementById('modal-img');
            let target_title = document.getElementById('modal-title');
            let target_client = document.getElementById('modal-client');

            target_img.setAttribute('src', src);
            target_title.innerHTML = title;
            target_client.innerHTML = client;
        }
    },
    data: () => {
        return {        
            active: false,
            show: false,
            projects: [
                {
                    id:1,
                    thumbnail: require("../assets/projects/project-1.png"),
                    title: "E-learning Web",
                    client: "example.sanggarinovasidesa.co.id",
                },
                {
                    id:2,
                    thumbnail: require("../assets/projects/project-2.png"),
                    title: "Online Shop Scattig",
                    client: "My Project",
                },
                {
                    id:3,
                    thumbnail: "",
                    title: "3",
                    client: "",
                },
                {
                    id:4,
                    thumbnail: "",
                    title: "4",
                    client: "",
                }
            ],
            itemToShow: 4,
        };
    }
};
</script>